@value variables: "./_variables.module.css";
@value colorHeader, colorSubHeaderDark, colorBg, headerHeight, shadow4, mobile from variables;


.SideNav {
  flex: 0 0 200px;
  position: relative;
}

@media mobile {
  .SideNav {
    position: fixed;
    z-index: 7000;
  }
}

.navContainer {
  position: fixed;
  z-index: 7000;
  width: 200px;
  top: headerHeight;
  bottom: 0;
  background-color: #fff;
  display: flex;
  flex-direction: column;
}

.navContent {
  overflow: scroll;
  overscroll-behavior: contain;
  -webkit-overflow-scrolling: touch;
  padding: 16px 0 80px;
  flex: 1 1 100%;
}

@media mobile {
  .navContent {
    padding-top: 0;
    box-shadow: shadow4;
  }
}

.scrollFader {
  position: relative;
}

.scrollFader:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 80px;
  pointer-events: none;
  background: linear-gradient(to bottom, rgba(245,245,245,0) 0%, rgba(245,245,245,1) 100%);
}

.appTitleBox {
  display: flex;
}

.appIcon {
  background-color: colorHeader;
  flex: 0 0 40px;
  height: 40px;
  border-radius: 0 3px 3px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
}

.appTitle {
  padding: 10px 8px 0;
  font-size: 16px;
  font-weight: 600;
  line-height: 18px;
  word-break: break-all;
}

.tinyTitle {
  text-transform: uppercase;
  font-size: 12px;
  color: colorSubHeaderDark;
  font-weight: 600;
  border-bottom: 1px solid colorSubHeaderDark;
  padding: 0 8px 0 16px;
  line-height: 23px;
  margin: 16px 0 8px;
}
